@import '../variables';
@import '../screenSisez';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @include media('max', 'sm') {
    padding: 20px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include media('max', 'md') {
      flex-direction: column;
    }
  }

  &__logo {
    display: flex;

    @include media('max', 'md') {
      margin-bottom: 10px;
    }

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
