@import '../variables';
@import '../screenSisez';

.pizza-block {
  width: 280px;
  text-align: center;
  @include media('max', 'xs') {
    width: $container-width;
  }

  &__image-container {
    width: 100%;
    height: 280px;
  }

  &__image {
    object-fit: contain;
    align-self: center;
    justify-self: center;
    width: 100%;
    height: 100%;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
    @include media('max', 'xs') {
      font-size: $font-size-small;
    }
  }
}
